@import "variables.scss";

.Workbook {
  display: flex;
  width: 100%;
  height: 100%;

  .heading {
    background: $navy;
    color: white;
    font-weight: bold;
    font-size: 0.7em;
    line-height: 1.8;
    text-transform: uppercase;
    padding: 0.5em;
    align-self: stretch;
  }

  .pages {
    background: lighten($ice, 2);
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;

    .heading {
      background: darken($color: $navy, $amount: 20)
    }
  }

  .thumbs {
    background: lighten($ice, 0);
  }

  .tools {
    background: lighten($ice, 0);
    flex-grow: 0;
  }

  .thumb,
  .page {
    background: white;
    box-shadow: $box-shadow;
    border: 1px solid rgba(0, 0, 0, 0.2);
  }

  .thumb {
    width: 150px;
    height: 280px;
    margin: 0 0 0.5em 0;
    padding: 1em;
    margin: 1em;
  }

  .page {
    width: 800px;
    height: 1200px;
    margin: 0.5em;
  }

  .doc {
    margin: 0 0 1em 0;
    text-align: center;
  }

  .toggle {
    background: rgb(162, 162, 162);
    mask-image: url("/img/compare.svg");
    mask-repeat: no-repeat;
    mask-size: contain;
    width: 60px;
    height: 60px;
    margin: 0.5em 1em;
  }

}