@import "variables.scss";

.SignIn {
  display: flex;
  background-image: url("/img/brain-bg.png");
  background-size: cover;
  height: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;


  .widget {
    position: relative;
    background: white;
    margin: 1em;
    min-width: 30vw;
    height: auto;
    border-top: 4px solid #91241D;
    box-shadow: $box-shadow;
  }

  .particles {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
  }

  .widget-content {
    padding: 2em;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 2;
  }

  .logo,
  .poweredByIgnite {
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 auto;
    width: 120px;
  }

  .logo {
    background-image: url("/img/KPMG-logo.svg");
    height: 50px;
  }

  .poweredByIgnite {
    background-image: url("/img/powered-by-ignite.svg");
    height: 80px;
  }

  .title,
  .titleLabel {
    text-transform: uppercase;
    color: $dark-navy;
    margin: 2em 0 1em 0;
    font-weight: bold;
  }

  .titleLabel {
    align-self: center;
    border-top: 0.3em solid $navy;
    padding-top: 0.5em;
  }

  form {
    display: flex;
    flex-direction: column;
    justify-items: right;
    margin-bottom: 2em;

    label {
      color: $navy;
      text-transform: uppercase;
      align-self: flex-start;
      font-weight: bold;
      font-size: 1em;
    }

    input {
      padding: 1em;
      margin: 0 0 1em 0;
      background: $ice;
      border: 0;
      border-radius: $border-radius;

    }

    input,
    button {
      outline: transparent 1px solid;
      transition: outline-color 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);

      &:focus {
        outline: darken($ice, 20%) 1px solid;
      }
    }
  }
}