@import "variables.scss";

.Header {
  border-bottom: 1px solid $teal;
  position: relative;

  .tabMenu {
    display: inline-flex;
    list-style: none;
    font-size: 0.9em;
    padding: 0 1em;

    li {
      padding: 0 1em;
      cursor: pointer;

      &.active {
        font-weight: bold;
      }

      a {
        color: $navy;
        text-transform: capitalize;
        user-select: none;
        text-decoration: none;

      }

    }
  }

  .HeaderContent {
    display: flex;
    justify-content: space-between;
    padding: 0 1em;
  }

  .activeMarker {
    position: absolute;
    border-bottom: 4px solid $navy;
    width: 200px;
    transition: all .4s cubic-bezier(0.215, 0.610, 0.355, 1);
    left: 0;
    bottom: 0;
  }
}